/**
 * Footer
 */
.footer-area {
  padding: 50px 0 15px 0;
  @extend .default-gradient;
  .single-footer-widget {
    @include media-breakpoint-down(md) {
      margin-bottom: 10px;
    }
    .brand-logos{
      margin-top: -1em;
      width: 160px;
      height: 60px;
    }
    h4 {
      color: $white;
      margin-bottom: 25px;
      font-size: 18px;
      @include media-breakpoint-down(lg)  {
        font-size: 18px;
      }
      @include media-breakpoint-down(md) {
        margin-bottom: 15px;
      }
    }
    ul {
      list-style: none;
      margin-left: -1.5em;
      li {
        margin-bottom: 5px;
        text-decoration: none;
        :before {
          content: "\2022";
          color: $white;
          font-weight: bold;
          display: inline-block;
          width: 1em;
          margin-left: -1em;
        }
        @include media-breakpoint-down(md) {
          text-decoration: none;
          display: inline;
          padding: 10px;
          margin: 0 5px 50px 0;
          background: #f9f9ff;
          border-radius: 15px;
        }
        a {
          @include transition();
          color: white;
          @include media-breakpoint-down(md) {
            color: $text-color;
          }
          &:hover {
            color: $text-color;
            @include media-breakpoint-down(md) {
              color: white;
            }
          }
          i {
            @include media-breakpoint-down(md) {
              display: none;
            }
          }
        }
      }
    }
  }

  .thrid-party {
    @include media-breakpoint-down(md) {
      display: none;
    }
  }
  .usefull-link {
    @include media-breakpoint-down(md) {
      display: none;
    }
  }
  .footer-bottom {
    margin-top: 2px;
    .footer-text {
      margin-top: 2px;
      color: white;
      a,
      span {
        color: $primary-color;
      }
    }
    .dmca-badge{
      float: right;
      img{
        float: right;
      }
    }
  }
}
