.section-category {
  position: relative;
  margin-top: -50px;
  height: 100px;
  .container {
    background: #fff;
    transition: $transition-base;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .2), 0 6px 20px 0 rgba(0, 0, 0, .19);
  }
  @include media-breakpoint-down(md) {
    display: none;
  }
}

.img-round{
  border-radius: 50%;
}

.section-userpost {
  padding: 40px 0 40px 0;
  .load-more{
    @extend .default-gradient;
    border-radius: 10px;
  }
}

.download {
  padding: .5em;

  &__android {
    position: relative;
    background-image: url(../assets/img/googleplay.svg);
    padding: 5px 10px;
    width: 135px;
    height: 40px;
  }

  &__ios {
    position: absolute;
    background-image: url(../assets/img/appstore.svg);
    padding: 5px 10px;
    width: 135px;
    height: 40px;
  }
}

.single-page {
  padding-top: 30px;
  padding-bottom: 20px;

  @include media-breakpoint-down(md) {
    padding-top: 10px;
  }
}

.carousel-single {
  padding: 20px 0 20px 0;

}

.sidebar {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  color: #555;
  padding: 0 15px;

  @include media-breakpoint-down(md) {
    display: none;
  }

  &__title {
    padding: 150;
    color: #555;

    a {
      color: #555;
    }


  }
}

.content {
  &__title {
    border-bottom: #a0a0a0 .5px solid;
  }
  &__desk{
    padding-top: 5px;
  }

  h1 {
    font-size: 30px;

    @include media-breakpoint-down(md) {
      font-size: 20px;
    }
  }
}

.active {
  color: #555;
  text-decoration: underline;
}

.font-single{
  font-size: 20px;
  padding-top: 10px;
  padding-bottom: 50px;
}
