.banner-area{
  background-image: url('//cdn.shopify.com/s/files/1/2030/2737/files/icon-seamless_ef568d79-394b-49ab-a3c5-128827d788e8.png?v=1496294246');
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .2), 0 6px 20px 0 rgba(0, 0, 0, .19);
  .home{
    height: 40vh;
    background: rgba(54, 154, 220, .8);
    .banner-content{
      margin-top: 15vh;
      color: #fff;
      h1{
        font-size: 40px;
      }
    }
    .video{
      margin-top: 12vh;
      .video-sect{
        width: 100%;
        height: 70%;
        a{
          .play{
            width: 60px;
            height: 60px;
            position: absolute;
            top: 20%;
            left: 40%;
          }
        }
        img{
          padding-left: 20px;
          padding-right: 20px;
          border-radius: 35px;
          width: 100%;
          opacity: .8;
          height: 100%;
        }
      }
      @include media-breakpoint-down(md) {
        display: none;
      }
    }
    .download-button{
      img{
        box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .2), 0 6px 20px 0 rgba(0, 0, 0, .19);
      }
    }
    @include media-breakpoint-down(md) {
      height: 25vh;
      .banner-content{
        margin-top: 10vh;
        color: #fff;
        text-align: center;
        h1{
          font-size: 25px;
        }
      }
      .download-button{
        img{
          height: 30px;
        }
      }
    }
  }
  .page{
    height: 20vh;
    background: rgba(54, 154, 220, .8);
    .banner-content{
      margin-top: 10vh;
      color: #fff;
      text-align: center;
      h1{
        font-size: 30px;
      }
    }
    .download-button{
      img{
        box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .2), 0 6px 20px 0 rgba(0, 0, 0, .19);
      }
    }
    @include media-breakpoint-down(md) {
      height: 20vh;
      .banner-content{
        margin-top: 10vh;
        color: #fff;
        h1{
          font-size: 25px;
        }
      }
      .download-button{
        img{
          height: 30px;
        }
      }
    }
  }
  .profile{
    height: 20vh;
    background: rgba(54, 154, 220, .8);
    .banner-content{
      margin-top: 7vh;
      color: #fff;
      text-align: center;
      h1{
        font-size: 25px;
        margin-bottom: 5px;
      }
    }
    .tab{
      padding-right: 10px;
    }
    @include media-breakpoint-down(md) {
      height: 30vh;
      .banner-content{
        margin-top: 10vh;
        color: #fff;
        h1{
          font-size: 25px;
        }
      }
    }
    @include media-breakpoint-up(md) {
      height: 30vh;
      .banner-content{
        margin-top: 10vh;
        color: #fff;
        h1{
          font-size: 25px;
        }
      }
    }
    @include media-breakpoint-up(lg) {
      height: 25vh;
      .banner-content{
        margin-top: 10vh;
        color: #fff;
        h1{
          font-size: 25px;
        }
      }
    }
  }
}
