/**
 * Global
 *
 * !default Flag
 * When building a library, a framework, a grid system or any piece of Sass
 * that is intended to be distributed and used by external developers,
 * all configuration variables should be defined with the !default flag
 * so they can be overwritten.
 */

$font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol",'Poppins','Roboto' !default;
$font-size-base: 1rem !default;
$line-height-base: 1.5 !default;
$title-font: "Poppins",sans-serif;
$text-font: "Roboto",sans-serif;
$grid-gutter-width:20px !default;
$grid-breakpoints: (xs: 0,sm: 576px,md: 768px,lg: 992px,xl: 1200px) !default;
$cards-per-line: (xs: 2,sm: 3,md: 3,lg: 4,xl: 5) !default;
@each $name,$breakpoint in $grid-breakpoints {
  @media (min-width: $breakpoint) {
    .card-deck .card {
      flex: 0 0 calc(#{100/map-get($cards-per-line, $name)}% - #{$grid-gutter-width});
    }
  }
}

