@import "@fortawesome/fontawesome-free/scss/brands.scss";
@import "@fortawesome/fontawesome-free/scss/regular.scss";
@import "@fortawesome/fontawesome-free/scss/solid.scss";

.love {
  @extend %fa-icon;
  @extend .fas;

  &:before {
    content: fa-content($fa-var-heart);
  }
}
.play-icon {
  width: 100px;
  height: 100px;
  color: $white;
  font-size: 4em;
  @extend %fa-icon;
  @extend .fas;

  &:before {
    content: fa-content(\f144);
  }
}

.facebook {
  @extend %fa-icon;
  @extend .fab;

  &:before {
    content: fa-content($fa-var-facebook-f);
  }
}

.instagram {
  @extend %fa-icon;
  @extend .fab;

  &:before {
    content: fa-content($fa-var-instagram);
  }
}

.pinterest {
  @extend %fa-icon;
  @extend .fas;

  &:before {
    content: fa-content(\f144);
  }
}

.social-icons {
  list-style: none;
  margin: 0;
  padding: .5em 0 .5em 0;

  span {
    display: block;
    width: 0;
    height: 0;
    overflow: hidden;
  }

  a {
    display: inline-block;
    border-radius: 50%;
    text-decoration: none;
    color: white;
    text-align: center;
    box-shadow: 0 2px 6px 0 rgba(0, 0, 0, .2), 0 6px 20px 0 rgba(0, 0, 0, .19);
    $icons: (twitter: (char: "\f099", color: #55acee),
    instagram: (char: "\f16d", color: #ea4c89),
    facebook: (char: "\f39e", color: #3b5998),
    pinterest: (char: "\f231", color: #dd4b39));

    @each $social,$icon in $icons {
      &[href*="#{$social}"] {
        background-color: map-get($icon, color);
        padding: 10px;
        @extend %fa-icon;
        width: 2.2em;
        height: 2.2em;
        @extend .fab;
        &:before {
          content: fa-content(map-get($icon, char));
        }
      }
    }
  }
}
