/**
 * Navbar
 */

$navbar: ('height-desktop': 60px,
'height-mobile': 40px,
'bg-color': #fafafa,
'link-color': $primary,
'hover-link-color': darken($primary, 15%),
'toggler-size': 8px,
'z-index': 1000,
'z-index-toggler': 1010);

@function navbar($key) {
  @return map-get($navbar, $key);
}

.navbar {
  // background: rgba(54, 154, 220, .5);
  z-index: navbar(z-index);

  &__wrapper {
    display: flex;
    align-items: center;
    height: navbar(height-mobile);
  }

  &__brand {
    color: navbar(link-color);

    &:hover {
      text-decoration: none;
      color: navbar(hover-link-color);
    }
  }

  &__collapse,
  &__toggler,
  &__toggler-link {
    transition: $transition-base;
  }

  &__collapse {
    opacity: 0;
    visibility: hidden;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    pointer-events: auto;
  }

  &__toggler {
    display: inline-block;
    position: relative;
    margin: 0;
    margin-left: auto;
    padding: 0;
    background: none;
    border: none;
    border-radius: 0;
    width: navbar(toggler-size) * 5;
    height: navbar(toggler-size) * 4;
    cursor: pointer;
    z-index: navbar(z-index-toggler);
  }

  &__toggler-line {
    display: block;
    position: absolute;
    top: navbar(toggler-size);
    left: navbar(toggler-size);
    background-color: $white;
    height: 2px;
    width: navbar(toggler-size) * 3;

    &:nth-child(2) {
      top: navbar(toggler-size) * 2;
    }

    &:nth-child(3) {
      top: navbar(toggler-size) * 3;
    }
  }

  &__menu {
    text-align: center;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    min-height: 100vh;
    @extend .default-gradient;
    padding: 2rem 0; // add some top / bottom padding in case there is more items that doesn't fit in the viewport
  }

  .menu-promo {
    position: relative;
    margin-top: 50px;
    bottom: 10px;

    .nav-banner-img {
      position: relative;
      text-align: center;

      .nav-img-fluid {
        height: 50%;
        margin-bottom: 20px;
      }
    }

    .download-button {
      bottom: 10px;
    }
  }

  .menu-item {
    a {
      color: #fafafa;
      margin: 5px;
    }

    &:not(:last-child) {
      margin-bottom: 1rem;
    }
  }

  body.navbar--opened & {
    &__collapse {
      visibility: visible;
      opacity: 1;
    }

    &__toggler-line {
      &:nth-child(1) {
        top: navbar(toggler-size) * 2;
        transform: rotate(45deg);
      }

      &:nth-child(2) {
        opacity: 0;
      }

      &:nth-child(3) {
        top: navbar(toggler-size) * 2;
        transform: rotate(-45deg);
      }
    }

    &__toggler {
      &:hover {
        transform: rotate(90deg);
      }
    }
  }

  // Media: Small devices and down

  @include media-breakpoint-down(sm) {
    &__wrapper {
      height: navbar(height-mobile);
    }

    &__collapse,
    &__toggler,
    &__toggler-link {
      transition: $transition-base;
    }

    &__collapse {
      opacity: 0;
      visibility: hidden;
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      overflow-x: hidden;
      overflow-y: auto;
      pointer-events: auto;
    }

    &__toggler {
      display: inline-block;
      position: relative;
      margin: 0;
      margin-left: auto;
      padding: 0;
      background: none;
      border: none;
      border-radius: 0;
      width: navbar(toggler-size) * 5;
      height: navbar(toggler-size) * 4;
      cursor: pointer;
      z-index: navbar(z-index-toggler);
    }

    &__toggler-line {
      display: block;
      position: absolute;
      top: navbar(toggler-size);
      left: navbar(toggler-size);
      background-color: $white;
      height: 2px;
      width: navbar(toggler-size) * 3;

      &:nth-child(2) {
        top: navbar(toggler-size) * 2;
      }

      &:nth-child(3) {
        top: navbar(toggler-size) * 3;
      }
    }

    &__menu {
      text-align: center;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      height: 100%;
      min-height: 100vh;
      background-color: navbar(bg-color);
      padding: 2rem 0; // add some top / bottom padding in case there is more items that doesn't fit in the viewport
    }

    .menu-promo {
      .nav-banner-img {
        .nav-img-fluid {
          width: 50%;
        }
      }

      .download-button {
        bottom: 10px;
      }
    }

    body.navbar--opened & {
      &__collapse {
        visibility: visible;
        opacity: 1;
      }

      &__toggler-line {
        &:nth-child(1) {
          top: navbar(toggler-size) * 2;
          transform: rotate(45deg);
        }

        &:nth-child(2) {
          opacity: 0;
        }

        &:nth-child(3) {
          top: navbar(toggler-size) * 2;
          transform: rotate(-45deg);
        }
      }

      &__toggler {
        &:hover {
          transform: rotate(90deg);
        }
      }
    }
  }

  // Media: Medium devices and up

  @include media-breakpoint-up(md) {
    &__wrapper {
      height: navbar(height-desktop);
    }

    &__brand {
      margin-right: auto;
    }

    &__menu {
      text-align: center;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      height: 100%;
      min-height: 90vh;
      background-color: navbar(bg-color);
      padding: 2rem 0;
    }
  }

  // Navbar Fixed

  &.navbar--fixed {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
  }

  // Make navbar transparent when scrolling down

  &.navbar--fixed-transparent.navbar--scrolled {
    @extend .default-gradient;
    transition: $transition-base;
    // opacity: .25;

    &:hover {
      opacity: 1;
    }
  }

  // Navbar slides out of view when scrolling down and slides back in when scrolling up

  &.navbar--fixed-showhide {
    transition: $transition-base;

    body.navbar--opened & {
      transform: none;
    }

    &.is-hidden {
      transform: translateY(-100%);
    }

    &.is-visible {
      transform: translateY(0);
      height: navbar(height-none);
    }
  }

}

// Prevent vertical scrollbar when navbar is opened on smaller devices

body.navbar--opened {
  overflow: hidden;
}

