.card {
  background: $card-bg;
  cursor: pointer;
  margin: 10px;
  padding: 5px;
  box-shadow: 0 0 .5px #555;
  position: relative;

  &__content-top {
    .card-right {
      float: right;
      padding-bottom: 5px;
    }

    .card-left {
      font-size: 16px;
      float: left;
      padding-bottom: 5px;
      a{
        margin-top: -15px;
      }
      span{
        position: absolute;
        padding-left: 45px;
        margin-top: -15px;
        font-size: 12px;
      }
    }
  }

  &__content-bottom {
    .card-right {
      position: absolute;
      right: 0;
      padding-right: 5px;
      padding-top: 5px;
    }

    .card-left {
      float: left;
      padding-top: 5px;
    }
  }

  &__button-top {
    position: absolute;
    margin: auto;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }

  &__button-center {
    position: absolute;
    margin: 0 auto;
    top: 50%;
    left: 50%;
    display: block;
    transform: translate(-50%, -50%);

    a {
      padding: 5px;
    }
  }

  &__img {
    width: 100%;
  }

  &__bottom {
    padding-top: 5px;

    .card-details {
      display: none;
    }

    .user-name {
      img {
        width: 20px;
        height: 20px;
        border-radius: 50%;
      }
    }

    .like-count {
      position: relative;
      color: #ccc;
      margin-top: 4px;
      @extend %fa-icon;
      @extend .fas;

      &:before {
        color: #a53d3d;
        content: fa-content($fa-var-heart);
      }
    }
  }

  @include media-breakpoint-down(md) {
    font-size: 12px;

    .like-count {
      display: none;
    }
  }

  @include media-breakpoint-up(md) {
    font-size: 12px;
  }

  .card-share {
    opacity: 0;
    position: absolute;
    padding: 5px 10px;
  }

  .cards-info-back {
    opacity: 0;
  }

  .cards-info-front {
    bottom: 0;

    .user-pict {
      width: 20px;
      height: 20px;
    }

  }

  &:hover {
    transition: $transition-base;
    box-shadow: 0 0 3px #555;

    .btn-pinterest {
      transition: $transition-base;
      display: inline;
      position: absolute;
      margin: 5px 0 0 5px;
      border-radius: 10px;
    }

    .user-name {
      display: none;
    }

    .like-count {
      display: none;
    }

    .card-details {
      display: inline;
      position: relative;
      text-align: center;

      a {
        color: #555;
        margin-top: 288px;
        width: 100%;
      }
    }

    .cards-info-front {
      opacity: 0;
    }

    .img-watt {
      opacity: 0;
    }

    .card-share {
      opacity: 1;

    }

  }
}
