/**
 * Carousel
 */

.carousel {
  &.is-hidden {
    display: none;
  }
  &.flickity-enabled {
    opacity: 1;
  }
  .carousel-cell {
    padding-left: 1rem;
    padding-right: 1rem;
    img{
      border-radius: 5px;
      width: 100%;
      height: 100px;
      margin-top: 10px;
      margin-bottom: 10px;
    }
    .cat-name{
      position: absolute;
      top: 35%;
      left: 35%;
      text-align: center;
      color: $white;
    }
    @include media-breakpoint-down(md) {
      width: 50%;
    }
    @include media-breakpoint-up(md) {
      width: 33.333%;
    }
    @include media-breakpoint-up(lg) {
      width: 20%;
    }
  }
  .flickity-page-dots{
    display: none;
  }
  .flickity-button{
    opacity: .1;
    @include media-breakpoint-down(md) {
      opacity: 0;
    }
  }
}
